import { OrderStatusEnum } from './../../enums/OrderStatusEnum';
import { Component, OnInit, ViewChild } from '@angular/core';
import { DatatableComponent } from '@swimlane/ngx-datatable';
import { GetPopularChemistService } from '../../services/GetPopularChemistService';
import { AddCategoryService } from '../../services/AddCategoryService';
import { GetChemistDataService } from '../../services/GetChemistDataService';
import { OrderService } from '../../services/OrderService';
import swal from 'sweetalert2';
import { Router } from '@angular/router';


@Component({
  selector: 'list-orders',
  templateUrl: './list-orders.component.html',
  styleUrls: [
    './list-orders.component.scss',
    '../../../../assets/icon/icofont/css/icofont.scss'
  ],
  providers: [AddCategoryService, GetPopularChemistService, GetChemistDataService, OrderService]
})
export class ListOrdersComponent implements OnInit {
  rowsBasic = [];
  fullScreenRow = [];
  loadingIndicator = true;
  reorderable = true;
  public OrderStatusEnum: OrderStatusEnum;
  rows = [];
  expanded = {};
  timeout: any;

  rowsFilter = [];
  tempFilter = [];

  rowsFilter2 = [];
  tempFilter2 = [];
  public chemists_array = [];
  public chemist_id: number = 0;
  public chemists_categories = [];
  public chemists_orders = [];
  public chemists_sub_categories = [];
  public category_name: string = "";
  public type: string = "";
  public updateformname: any;
  @ViewChild(DatatableComponent) table: DatatableComponent;
  @ViewChild(DatatableComponent) table2: DatatableComponent;
  public showAttr: boolean = false;
  constructor(public _addCategoryService: AddCategoryService,
    public route:Router,
    public _getChemistDataService: GetChemistDataService,
    public _getPopularChemistService: GetPopularChemistService,
    public _ordersService: OrderService) {
    var session_obj = JSON.parse(localStorage.getItem('session_obj'));
    if (session_obj.UserRole == 1) {
      this.getChemists();
      this.showAttr = true;
    }
    else if (session_obj.UserRole == 2) {
      this.chemist_id = session_obj.ChemistId;
      this.showAttr = false;
      this.getOrdersOfChemists();
    }
    // if(orderstatus == this.OrderStatusEnum.)
  }


  viewSubCategories(Id) {
    var category = this.chemists_categories.find(x => x.Id == Id);
    this.category_name = category.CategoryName;
    this._addCategoryService.getAllSubCategoryValues(this.chemist_id, Id).subscribe(response => {
      if (response.code == 200) {
        console.log(response.data);
        this.chemists_sub_categories = response.data;
        this.tempFilter2 = this.chemists_sub_categories;
        this.rowsFilter2 = this.chemists_sub_categories;
        $('#sub-cateories-list').show();
      }
    });
  }// End of Get SubCategories

  getCategoriesOfChemists() {
    $('#categories-fetcher').show();
    $('#sub-cateories-list').hide();
    this._addCategoryService.getCateogry(this.chemist_id).subscribe(
      response => {
        $('#categories-fetcher').hide();
        console.log(response.data);
        $('#list').show();
        this.chemists_orders = response.data;
        this.tempFilter = this.chemists_categories;
        this.rowsFilter = this.chemists_categories;
      });
  }// End of Get Categories Of Chemists

  getChemists() {
    this._getChemistDataService.getRegisteredChemistService().subscribe(
      response => {
        console.log(response);
        for (var i = 0; i < response.data.length; i++) {
          this.chemists_array.push(response.data[i].Chemist);
        }
        //this.chemists_array = response.data;
      });
  }// End of Get Chemists

  getOrdersOfChemists() {
    $('#categories-fetcher').show();
    $('#sub-cateories-list').hide();
    this._ordersService.getOrdersByChemistId(this.chemist_id).subscribe(
      response => {
        $('#categories-fetcher').hide();
        console.log(response.data);
        $('#list').show();
        if (response.data == null) {
          this.chemists_orders = [];
        }
        else {
          this.chemists_orders = response.data;

          // this.chemists_orders.forEach(element => {
          //   if(element.OrderStatus == 100){
          //     element.OrderStatus = this.OrderStatusEnum.PENDING;
          //   }
          // });

          
          for (var i = 0; i < this.chemists_orders.length; i++) {
            if (this.chemists_orders[i].OrderStatus == 100) {
              this.chemists_orders[i].OrderStatus = "Pending";
            }else if(this.chemists_orders[i].OrderStatus == 200){
              this.chemists_orders[i].OrderStatus = "Confirmed";
            }else if(this.chemists_orders[i].OrderStatus == 300){
              this.chemists_orders[i].OrderStatus = "On The Way";
            }else if(this.chemists_orders[i].OrderStatus == 400){
              this.chemists_orders[i].OrderStatus = "Not Shipped";
            }else if(this.chemists_orders[i].OrderStatus == 500){
              this.chemists_orders[i].OrderStatus = "Shipped";
            }else if(this.chemists_orders[i].OrderStatus == 600){
              this.chemists_orders[i].OrderStatus = "To Be Delivered";
            }else if(this.chemists_orders[i].OrderStatus ==700){
              this.chemists_orders[i].OrderStatus = "Canceled";
            }else if(this.chemists_orders[i].OrderStatus ==800){
              this.chemists_orders[i].OrderStatus = "Completed";
            }else if(this.chemists_orders[i].OrderStatus ==900){
              this.chemists_orders[i].OrderStatus = "Returned";
            }else if(this.chemists_orders[i].OrderStatus ==1000){
              this.chemists_orders[i].OrderStatus = "Delivered";
            }
          }

          for(var i=0; i<this.chemists_orders.length; i++){
            this.chemists_orders[i].CreatedOnUTC = this.chemists_orders[i].CreatedOnUTC *1000;
          }

          this.rowsFilter = this.chemists_orders;
          console.log("Orders are showing");
          console.log(this.chemists_orders);
        }
        this.tempFilter = this.chemists_orders;
        this.rowsFilter = this.chemists_orders;
      });
  }
  viewOrderDetail(id){
    
    var element = this.chemists_orders.find(chemist => chemist.Id == id);
    console.log(element);

    localStorage.setItem('editOrder', JSON.stringify(element));

    this.route.navigateByUrl('/admin/orders/edit-order?id='+id); 


  }//View Order Detail


  openUpdateCategory(Id) {
    this.type = "Category";
    var category_obj = this.chemists_categories.find(x => x.Id == Id);
    this.updateformname = category_obj.CategoryName;
  }// Update Category End

  openUpdateSubCategory(Id) {
    this.type = "Sub Category";
    var category_obj = this.chemists_sub_categories.find(x => x.Id == Id);
    this.updateformname = category_obj.CategoryName;
  }// Update Sub Category End

  update() {
    $('.text-danger').hide();
    if (this.updateformname === undefined || this.updateformname == "" || this.updateformname == null) {
      $('#error-message').show();
      return;
    }
    if (this.type == "Category") {

    }
    else {
      //Sub Category
    }
  }// End of Update

  updateFilter2(event) {
    const val = event.target.value.toLowerCase();

    // filter our data
    const temp = this.tempFilter2.filter(function (d) {
      return d.CategoryName.toLowerCase().indexOf(val) !== -1 || !val;
    });

    // update the rows
    this.rowsFilter2 = temp;
    // Whenever the filter changes, always go back to the first page
    this.table2.offset = 0;
  }

  updateFilter(event) {
    const val = event.target.value.toLowerCase();

    // filter our data
    const temp = this.tempFilter.filter(function (d) {
      return d.CategoryName.toLowerCase().indexOf(val) !== -1 || !val;
    });

    // update the rows
    this.rowsFilter = temp;
    // Whenever the filter changes, always go back to the first page
    this.table.offset = 0;
  }
  //Delete Services
  deleteCategoryServices(i) {
    // this.measurement.splice(i, 1);
    console.log(i);
    swal({
      title: 'Are you sure?',
      text: 'You wont be able to revert',
      type: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Yes, delete it!'
    }).then(function (result) {
      if (result.value == true) {
        swal(
          'Deleted!',
          'Package has been deleted.',
          'success'
        );
      }
      else {
        // alert('No deletion');
      }
    }).catch(swal.noop);
  }
  onPage(event) {
    clearTimeout(this.timeout);
    this.timeout = setTimeout(() => {
      console.log('paged!', event);
    }, 100);
  }

  getRowHeight(row) {
    return row.height;
  }
  ngOnInit() {
  }
}



